<template>
<v-content>
  <v-container class="screen">
  <v-row class="fill-height" >
    <v-col>
      <v-sheet height="64" width="1290">
        <v-toolbar
          flat
        >
          <v-select 
            v-model="regionSeleccionada"
            :items="regionsDescriptions"
            label="Región"
            placeholder="Selecciona..."
            outlined
            dense
            @input="eventsFromRegion"
            style="margin-bottom:4px; margin-top:2rem; max-width:12rem"
            >
            </v-select>
          <v-btn
            fab
            text
            small
            color="grey darken-2"
            style="margin-left:1rem"
            @click="prev"
          >
            <v-icon small>
              mdi-chevron-left
            </v-icon>
          </v-btn>
          <v-btn
            fab
            text
            small
            color="grey darken-2"
            @click="next"
          >
            <v-icon small>
              mdi-chevron-right
            </v-icon>
          </v-btn>
          <v-toolbar-title style="margin-left: 18rem">
            {{title}}
          </v-toolbar-title>
          <v-spacer></v-spacer>
          <router-link style="position:relative;right:14.5%" to="/periodos">
          <v-btn v-b-tooltip.hover  title="Activar o desactivar períodos"  dark rounded color="#2991c6"> 
            <v-icon dark >mdi-lock-open</v-icon> 
            <v-icon dark >mdi-lock</v-icon> 
          </v-btn>
          </router-link>
          <v-btn
            dark 
            v-if="permisoActualizarCalendario()"
            fab 
            small 
            color="#2991c6"
            @click="dialog = true"
          >
            <v-icon dark>mdi-plus</v-icon>
          </v-btn>
        </v-toolbar>
      </v-sheet>
      <v-sheet height="450" width="1290">
        <v-calendar
          ref="calendar"
          v-model="focus"
          locale="es"
          color="primary"
          :events="events"
          :event-color="getEventColor"
          type="month"
          @click:event="showEvent"
          @change="updateRange"
        ></v-calendar>

          <!-- DIALOGO PARA AGREGAR EVENTO -->

          <v-dialog v-model="dialog" width="350">
            <v-card>
              <v-container>
                <v-form @submit.prevent="addEvent">
                  <v-select :items="typeEvents" placeholder="Selecciona..." v-model="newEvent.type" label="Tipo">
                  </v-select>
                  <v-text-field width="200" type="text" label="Feriado" placeholder="Descripción" v-model="newEvent.name">
                  </v-text-field>
                  <v-select 
                  v-model="newEvent.region"
                  :items="regionsDescriptions"
                  label="Región"
                  placeholder="Selecciona..."
                  >
                  </v-select>
                  <v-text-field style="width:9rem; display:inline-block" type="date" label="Feriado" v-model="newEvent.start">
                  </v-text-field>
                  <v-btn style="margin-left:4rem;display:inline-block" type="submit" color="#2991c6"
                  @click.stop="dialog = false"> AGREGAR
                  </v-btn>
                </v-form>
              </v-container>
            </v-card>
          </v-dialog>

          <!-- DIALOGO PARA AGREGAR EVENTO -->

        <v-menu
          v-model="selectedOpen"
          :close-on-content-click="false"
          :activator="selectedElement"
          offset-x
        >
          <v-card
            color="grey lighten-4"
            min-width="350px"
            flat
          >
            <v-toolbar
              :color="selectedEvent.color"
              dark
            >
              <v-btn icon>
                <v-icon>mdi-pencil</v-icon>
              </v-btn>
              <v-toolbar-title v-html="selectedEvent.name"></v-toolbar-title>
              <v-spacer></v-spacer>
              <v-btn icon>
                <v-icon>mdi-heart</v-icon>
              </v-btn>
              <v-btn icon>
                <v-icon>mdi-dots-vertical</v-icon>
              </v-btn>
            </v-toolbar>
            <v-card-text>
              <span v-html="selectedEvent.details"></span>
            </v-card-text>
            <v-card-actions>
              <v-btn
                text
                color="secondary"
                @click="selectedOpen = false"
              >
                Cancel
              </v-btn>
            </v-card-actions>
          </v-card>
        </v-menu>
      </v-sheet>
    </v-col>
  </v-row>
  </v-container>
  </v-content>
</template>

<style scoped>
.screen {
  margin-left: 1rem;
  min-width: 100rem;
}
</style>

<script>
import axios from "axios";
const ip = require('../../../ip/ip')
  export default {
    data: () => ({
      title: '',
      focus: '',
      type: 'month',
      typeToLabel: {
        month: 'Month',
      },
      start: null,
      end: null,
      selectedEvent: {},
      selectedElement: null,
      selectedOpen: false,
      events: [],
      eventsFromApi: [],
      newEvent: {
        type: '',
        name: '',
        region: '',
        start: null,
        color: "blue"},
      regionsDescriptions: [],
      regiones: [],
      typeEvents : ['Feriado', 'Notificacion'],
      name: '',
      dialog: false,
      monthName:'',
      regionSeleccionada: '',
    }),
    created(){
      this.getEvents()
      this.loadRegions();
      console.log(this.$refs.calendar)
      //this.getNotifications()
    },

    mounted () {
      this.$refs.calendar.checkChange()
    },
      
    methods: {

      //Metodos que brinda la interfaz provista por vuetify
      getEventColor (event) {
        return event.color
      },
      setToday () {
        this.focus = ''
      },
      prev () {
        this.$refs.calendar.prev()
      },
      next () {
        this.$refs.calendar.next()
      },
      showEvent ({ nativeEvent, event }) {
        const open = () => {
          this.selectedEvent = event
          this.selectedElement = nativeEvent.target
          setTimeout(() => {
            this.selectedOpen = true
          }, 10)
        }

        if (this.selectedOpen) {
          this.selectedOpen = false
          setTimeout(open, 10)
        } else {
          open()
        }

        nativeEvent.stopPropagation()
      },
      updateRange({ start, end }) {
        this.start = start;
        this.end = end;

        let yr = this.start.year;
        let mn = this.start.month;

        const mNames = [
          "Enero",
          "Febrero",
          "Marzo",
          "Abril",
          "Mayo",
          "Junio",
          "Julio",
          "Agosto",
          "Septiembre",
          "Octubre",
          "Noviembre",
          "Diciembre",
        ];

        this.title = mNames[mn-1] + " - " + yr;
      },


      //Pedido a la api de feriados
      async getEvents(){
        await axios.get(ip+"/feriados").then(response =>{
          this.eventsFromApi = response.data
        })
        for(var i=0 ; i<this.eventsFromApi.length ; i++){
          this.events.push({name: this.eventsFromApi[i].Feriado_Descripcion , start:this.eventsFromApi[i].Feriado_Fecha, end: this.eventsFromApi[i].Feriado_Fecha, color:"blue"})
        }
      },

      //Pedido a la api de notificaciones
      async getNotifications(){
        await axios.get(ip+"/tiempos")
      },

      //Pedido de regiones
      loadRegions() {
      axios.get(ip+"/regiones/descripciones")
        .then((response) => {
          this.regiones = response.data;
          this.regionsDescriptions = response.data.map((region) => region.Region_Descripcion);
        });
      },

      //Agrupa feriados de la region seleccionada
      async eventsFromRegion() { 
        var regionKey = this.assignKey();
        console.log(regionKey)
        this.events = []
        await axios.get(ip+"/feriados/"+regionKey).then(response => {
          this.eventsFromApi = response.data
        })
        for(var i=0 ; i<this.eventsFromApi.length ; i++){
          this.events.push({name: this.eventsFromApi[i].Feriado_Descripcion , start:this.eventsFromApi[i].Feriado_Fecha, end: this.eventsFromApi[i].Feriado_Fecha, color:"blue"})
        }
      },


      //Agregado de evento o feriado
      addEvent(){
        if(this.newEvent.type == 'Feriado'){
        var saveFeriado = {
          Feriado_Region: this.regiones.filter((region) => region.Region_Descripcion == this.newEvent.region)[0].Region_Key,
          Feriado_Tiempo: this.convertDateToInt(this.newEvent.start),
          Feriado_Fecha: this.newEvent.start,
          Feriado_Descripcion: this.newEvent.name,
          Feriado_Laborable: '',
          Usuario_Creacion: 1,
          Usuario_Modificacion: 1
        }
        this.events.push(this.newEvent)
        axios.post(ip+'/feriados',saveFeriado).then(response =>{
          alert('Evento agregado al calendario')
        })
        } else {

        }
        this.newEvent= {
          name: '',
          region: '',
          start: null,
          color: "blue"
        }
      },

      //Convierte una fecha date a INT
      convertDateToInt(date){
        var year = date.substring(0,4)
        var month = date.substring(5,7)
        var day = date.substring(8,10)
        var dateToSave = year+month+day
        console.log(dateToSave)
        return Number.parseInt(dateToSave)
      },


      //Cambia la descripcion del feriado a int para el guardado en la base
      assignKey(){
        var keyRegion =  this.regiones.filter((region) => region.Region_Descripcion == this.regionSeleccionada)[0].Region_Key;
        return keyRegion
      },

      permisoActualizarCalendario(){
       return localStorage.Permisos.includes("P30")
         
      },

      permisoActualizarPeriodos(){
        return localStorage.Permisos.includes("P26")
      },


      rnd (a, b) {
        return Math.floor((b - a + 1) * Math.random()) + a
      },
      mounted() {
      if(!localStorage.getItem('token')){
      this.$router.push("/login");
      }}       
    },
  }
</script>