<template>
  <v-content>
    <v-container fluid>
      <v-form ref="form">
      <v-row>
        <v-col cols="12" style="max-width:82rem; margin-top:-0.5rem">
          <v-title style="font-size:1.5rem; padding-left:1rem;">Datos Generales</v-title>
          <img style="margin-top:-1rem; margin-left:1rem" width="180rem" height="40rem" v-bind:src="empresa.Empresa_Logo">
          <v-btn @click="guardar" :disabled="permisoGuardar()" color="#32b4dd" dark style="margin-left:40rem">Guardar</v-btn>
          <router-link to="/empresasTable">
          <v-btn @click="cleanStore" color="#ffa025" dark style="margin-left:1rem">Cancelar</v-btn>
          </router-link>
          <v-card class="pa-2" outlined tile style="margin-top:1rem;">
            <v-text-field
              outlined
              dense
              :disabled="permisoGuardar()"
              v-model="empresa.Empresa_Codigo"
              label="Código"
              :rules="[rules.checkCode,rules.counterCodigo]"
              placeholder="Escribe..."
              style="display:inline-block; max-width:10rem;"
            ></v-text-field>
            <v-text-field
              outlined
              dense
              :disabled="permisoGuardar()"
              v-model="empresa.Empresa_Descripcion"
              label="Descripción"
              :rules="[rules.checkCode,rules.counterDescripcion]"
              placeholder="Escribe..."
              style="display:inline-block; min-width:35rem; margin-left:1rem"
            ></v-text-field>
            <v-text-field
              outlined
              background-color="white"
              dense
              :disabled="permisoGuardar()"
              v-model="empresa.Empresa_Identificador_Laboral"
              label="ID Laboral"
              :rules="[rules.checkNumbers]"
              placeholder="Escribe..."
              style="display:inline-block; min-width: 19rem;margin-left:1rem"
            ></v-text-field>
            <v-select
              dense
              :disabled="permisoGuardar()"
              outlined
              v-model="empresa.Empresa_Tipo"
              :items="tiposEmpresasDescripciones"
              label="Tipo"
              placeholder="Selecciona..."
              style="max-width:12rem; display:inline-block;margin-left:1rem"
            ></v-select>
            <v-textarea v-model="empresa.Empresa_Observacion" background-color="white" :disabled="permisoGuardar()" outlined placeholder="Escribe.." :counter="1000" label="Observaciones" rows="2" style="margin-bottom:-2rem">
           </v-textarea>
          </v-card>
        </v-col>
      </v-row>
      </v-form>
    </v-container>
  </v-content>
</template>

<style  scoped>
.container {
  margin-left: 1rem;
  min-width: 100rem;
}
</style>


<script>
import axios from "axios";
const ip = require('../../../ip/ip')
import {checkCode,checkEmail,counterTelefono,counterCodigo, counterDescripcion,
        counterEmail,checkPhoneNumber,checkNumbers,counterReferentes} 
from '../../../validations/validations'

export default {
  data: () => ({
    codigoViejo: '',
    //Reglas de agregado y editado
    rules: {
      checkCode: checkCode,
      checkEmail: checkEmail,
      checkPhoneNumber: checkPhoneNumber,
      checkNumbers: checkNumbers,
      counterCodigo: counterCodigo,
      counterDescripcion: counterDescripcion,
      counterTelefono: counterTelefono,
      counterEmail: counterEmail,
      counterReferentes: counterReferentes,
    },
    //Elemento que se va a guardar con el UPDATE
    empresa: {
        Empresa_Codigo: '',
        Empresa_Descripcion: '',
        Empresa_Tipo: null,
        Empresa_Identificador_Laboral: '',
        Empresa_Logo: 'https://iwantic.com/wp-content/uploads/2016/12/consultor-sap.png',
        Empresa_Observacion: '',
        Usuario_Creacion: 1,
        Usuario_Modificacion: 1,
    },

    tiposEmpresas: [],
    tiposEmpresasDescripciones: [],
  }),

  created(){
      this.initialize();
  },

  methods: {
    async initialize(){
      this.loadEmpresa();
      this.loadTiposEmpresas();
      await this.wait(1000); //DISPARAR EVENTO DE LOADING
      this.asignarDescripciones();
    },

      wait(time) {
      return new Promise(resolve => {
        setTimeout(() => {
          resolve();
        }, time);
  })
    },

    loadEmpresa(){
        this.empresa = this.$store.state.empresa[0];
        this.codigoViejo = this.$store.state.empresa[0].Empresa_Codigo;
    },

    loadTiposEmpresas(){
        axios.get(ip+"/tipos_empresas/descripciones")
        .then(response => {
            this.tiposEmpresas = response.data
            this.tiposEmpresasDescripciones = response.data.map(tipo_Empresa => tipo_Empresa.Tipo_Empresa_Descripcion)
        })      
    },

    guardar() {
        if(this.$refs.form.validate()){
          this.asignarKeys();
          axios.patch(ip+"/empresas/"+ this.codigoViejo, this.empresa)
          .then((response) => {
              alert(response.data)
          })
          .catch(err =>{
            alert('No se ha podido actualizar. La empresa ya existe')
            throw new Error('La empresa ya existe')
          })
        }
    },

    cleanStore(){
      this.$store.replaceState({
        cliente: [],
        clienteTipo:0,
        proyecto: [],
        clienteFiscal: [],
        empresa: []})
    },

    permisoGuardar(){
      return !localStorage.Permisos.includes("P14")
    },

    asignarKeys(){
        var tipoEmpresaKey = this.tiposEmpresas.filter((tipoEmpresa) => tipoEmpresa.Tipo_Empresa_Descripcion == this.empresa.Empresa_Tipo)[0].Tipo_Empresa_Key;
        this.empresa.Empresa_Tipo = tipoEmpresaKey;
    },

    asignarDescripciones(){
        var tipoEmpresaDesc = this.tiposEmpresas.filter((tipoEmpresa) => tipoEmpresa.Tipo_Empresa_Key == this.empresa.Empresa_Tipo)[0].Tipo_Empresa_Descripcion;
        this.empresa.Empresa_Tipo = tipoEmpresaDesc;
    },
  mounted() {
      if(!localStorage.getItem('token')){
      this.$router.push("/login");
      }} 
  },
};
</script>